                            </el-button>
                            <el-dropdown-menu slot="dropdown" class="lang-select-dropdown">
                                <el-dropdown-item v-for="host in this.hosts" :key="host.value"
                                    @click.native="refresh(host.value)">
                                    {{ host.value }}
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </el-col>
                <el-col :span="11">
