<template>
    <div>
        <!--
          el-pagination 
            small	
            background		
            page-size	 .sync 
            total	
            current-page	 .sync 	
            layout	
            page-sizes	
          event:
            size-change	pageSize 
            current-change	currentPage  

        -->
        <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="10"
      layout="total, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </div>
</template>

<script>
    export default {
      props:{
            total:{
              type:Number,
              default:100
            }
      },
        methods: {
          //8
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      //
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.$emit('CurrentChange',val);
      }
    },
    data() {
      return {
        currentPage: 1
        //1
      };
    }
    }
</script>

